import apiCore from "@/utils/apiCore";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});


export function getItemPage(query:any) {
    return HTTP.get('/sys/dict/item/page', query);
}

export function addObj(obj:any) {
    return HTTP.post('/sys/dict/item', obj);
}


export function delItemObj(id:any) {
    return HTTP.delete('/sys/dict/item/' + id, {});
}

export function putObj(obj:any) {
    return HTTP.post('/sys/dict/item/updateInfo', obj);
}
