import {Component, Vue} from 'vue-property-decorator';
// import {getSelect} from '@/api/manage/sys/sysUserApi'

@Component({})
export class tablePageMixin extends Vue {
    min_height: any = window.innerHeight - 320;
    userInfo: any = Vue.ls.get("USER_INFO");
    orgInfo: any = Vue.ls.get("ORG_INFO");
    popVisible: number = 0;
    rowKey: string = 'id';
    formPage: any;
    // 表格展示
    tableShow: boolean = true
    //表单初始化标识
    formInited: boolean = false
    // 数据数组
    dataSource: Array<object> = [];
    // 数据加载动画
    loading: boolean = false;
    // 分页器设置
    pagination: any = {
        total: 0,
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        showTotal: (total: number, range: any) => {
            return range[0] + '-' + range[1] + ' 共' + total + '条'
        }
    }
    // 查询参数
    queryParam: any = {}
    // 默认排序字段
    sortField: string = 'createTime';
    // 筛选参数字段
    filters: object = {};
    // 升序/降序
    sortAsc: boolean = false;
    // 高级搜索 展开/关闭
    advanced: boolean = false;
    // 表单页的title
    cardTitle: string = '';
    // 列表页的title
    pageTitle: string = '';

    isConditionText: string = "展开条件";

    toggle(): void {
        this.isConditionText = this.isConditionText == '展开条件' ? '收起条件' : '展开条件';
    }

    setSearchCondition(condition: any): void {
        this.queryParam = condition;
        this.reloadTable();
    }

    dateFtt(dateStr: string, fmt: any) { // author: meizz
        var date = new Date(dateStr);
        const o: any = {
            'M+': date.getMonth() + 1,                 // 月份
            'd+': date.getDate(),                    // 日
            'h+': date.getHours(),                   // 小时
            'm+': date.getMinutes(),                 // 分
            's+': date.getSeconds(),                 // 秒
            'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
            'S': date.getMilliseconds(),             // 毫秒
        };
        if (/(y+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (const k in o) {
            if (new RegExp('(' + k + ')').test(fmt)) {
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1)
                    ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
            }
        }
        return fmt;
    }

    addZero(v:any, size:any):any {
        for (var i = 0, len = size - (v + '').length; i < len; i++) {
            v = '0' + v
        }
        return v + ''
    }

    formatterDate(row: any, column: any, cellValue: any, index: any): any {
        if (cellValue != null) {
            const  date:any =  new Date(cellValue)
            const  formatStr = 'YYYY-MM-DD HH:II:SS'
            var arrWeek = ['日', '一', '二', '三', '四', '五', '六']
            var str = formatStr.replace(/yyyy|YYYY/, date.getFullYear())
                .replace(/yy|YY/, this.addZero(date.getFullYear() % 100, 2))
                .replace(/mm|MM/, this.addZero(date.getMonth() + 1, 2))
                .replace(/m|M/g, date.getMonth() + 1)
                .replace(/dd|DD/, this.addZero(date.getDate(), 2))
                .replace(/d|D/g, date.getDate())
                .replace(/hh|HH/, this.addZero(date.getHours(), 2))
                .replace(/h|H/g, date.getHours())
                .replace(/ii|II/, this.addZero(date.getMinutes(), 2))
                .replace(/i|I/g, date.getMinutes())
                .replace(/ss|SS/, this.addZero(date.getSeconds(), 2))
                .replace(/s|S/g, date.getSeconds())
                .replace(/w/g, date.getDay())
                .replace(/W/g, arrWeek[date.getDay()])
            return str

        }
    }

    // 获取分页数据的方法
    getPage(params: any): any {
    };

    //获取数据成功后回调函数
    loadDataSuccess(res: any): any {

    }

    // formatterEmpName(row: any, column: any, cellValue: any, index: any): any {
    //     if (cellValue != null) {
    //         const item = this.empItem().find((item: any) => item.value === cellValue.toString()) || {};
    //         return (item && item.name) || cellValue;
    //     }
    // }

    // empItem() {
    //     if (Vue.ls.get("empData") == null) {
    //         getSelect().then((res: any) => {
    //             Vue.ls.set("empData", res.data);
    //         });
    //     }
    //     return Vue.ls.get("empData");
    // }

    // 删除 数据的方法
    delObj(id: any): any {
    };

    created() {
        this.loadData()
    }

    /**
     * 表格重新加载方法
     * 如果参数为 true, 则强制刷新到第一页
     * @param bool
     */
    reloadTable(bool = false) {
        bool && (this.pagination.current = 1)
        this.loadData()
    }

    /**
     * 表格数据加载方法
     */
    loadData() {
        // 合并查询参数，分页参数，排序参数，过滤参数
        this.loading = true;
        const params = Object.assign(this.queryParam, {
            current: this.pagination.current,
            size: this.pagination.pageSize
        }, {
            sortField: this.sortField,
            sortAsc: this.sortAsc
        }, {...this.filters})

        this.getPage(params)
            .then((res: any) => {
                console.log('res', res);
                const page = res
                // 为防止删除数据后导致页面当前页面数据长度为 0 ,自动翻页到上一页
                if (page.records.length === 0 && this.pagination.current > 1) {
                    this.pagination.current--
                    this.loadData()
                    return
                }
                this.dataSource = page.records
                console.log(this.dataSource);
                this.pagination.total = page.total
                this.loadDataSuccess(res);
            }).catch((e: any) => {
            this.$message.error(e.message || 'error request')
        }).finally(() => {
            this.loading = false
        })
    }

    // ========== PageForm交互 ===================
    // TODO 支持 modal 形式表单
    // 切换表格/表单
    switchPage() {
        window.scrollTo(0, 0)
        this.tableShow = !this.tableShow
        if (!this.formInited) {
            this.formInited = true
        }
    }

    // 返回表格
    backToPage(needRefresh: boolean) {
        this.switchPage();
        needRefresh && this.reloadTable(false)
    }

    handleSizeChange(val: number): void {
        this.pagination.pageSize = val;
        this.loadData();
    }

    // 新增
    handleAdd() {
        this.switchPage()
        this.cardTitle = this.pageTitle + '新增'
        this.$nextTick(function () {
            this.formPage.add()
        })
    }


    handleEdit(record: any, title: string) {
        this.switchPage()
        this.cardTitle = '“' + title + '”' + ' 修改'
        this.$nextTick(function () {
            this.formPage.update(record)
        })
    }

    // 删除
    handleDel(record: any) {
        this.delObj(record[this.rowKey]).then((res: any) => {
            if (res.code === 200) {
                this.$message.success(res.msg)
                this.reloadTable()
            } else {
                this.$message.error(res.msg)
            }
        })
    }

    handleCurrentChange(val: number) {
        this.pagination.current = val;
        this.loadData();
    }
}
