































import {Component, Mixins, Prop, Vue} from 'vue-property-decorator';
import {formPageMixin} from '@/mixins/formPageMixin';
import {addObj, putObj} from '@/api/manage/sys/sysDictItemApi';

@Component({
  components: {}
})
export default class SysDictItemForm extends Mixins(formPageMixin) {
  @Prop() title!: string;
  @Prop() parentData!: any;

  reqFunctions = {
    create: addObj,
    update: putObj
  };

  createdFormCallback() {
    this.formObj.dictCode = this.parentData.code;
    this.formObj.dict_name = this.parentData.title;
  }

  echoDataProcess(record: any) {
    record.dict_name = this.parentData.title;
  }

  formRules = {
    name: [
      {required: true, message: '请输入文本值', trigger: 'blur'}
    ],
    value: [
      {required: true, message: '请输入数据值', trigger: 'blur'}
    ]
  };
}
