






























import {Component, Mixins, Prop, Vue} from "vue-property-decorator";
import {formPageMixin} from "@/mixins/formPageMixin";
import {addObj, putObj} from "@/api/manage/sys/sysDictApi";

@Component({
  components: {}
})
export default class SysDictForm extends Mixins(formPageMixin) {
  @Prop() title!: string;
  reqFunctions = {
    create: addObj,
    update: putObj
  }

  createdFormCallback() {
    this.formObj={editable:1};
  }

  formRules = {
    title: [
      {required: true, message: '请输入名称', trigger: 'blur'}
    ],
    code: [
      {required: true, message: '请输入标识', trigger: 'blur'}
    ]
  }
}
