













































































































import {Component, Mixins, Vue} from 'vue-property-decorator';
import {tablePageMixin} from '@/mixins/tablePageMixin';
import {getPage, delObj} from '@/api/manage/sys/sysDictApi';
import {getItemPage, delItemObj} from '@/api/manage/sys/sysDictItemApi';
import SysDictForm from '@/views/manage/sys/dict/SysDictForm.vue';
import SysDictItemForm from '@/views/manage/sys/dict/SysDictItemForm.vue';

@Component({
  components: {
    SysDictForm,
    SysDictItemForm
  }
})
export default class SysDictPage extends Mixins(tablePageMixin) {
  pageTitle: string = '数据字典';
  getPage = getPage;
  delObj = delObj;
  formItemPage: any;
  dictCodes: string = '';
  dictData: any = [];
  activeIndex: number = 0;
  editable: boolean = false;
  dictItem: any = {};
  delPopVisible: number = 0;

  mounted() {
    this.pagination.pageSize = 3000
    this.reloadTable(true)
    this.formPage = this.$refs.formPage;
    this.formItemPage = this.$refs.formItemPage;

  }

  loadDataSuccess(res: any) {
    this.loadDictData(res.records[0]);
  }

  loadDictData(dict: any) {
    this.dictItem = dict;
    this.queryParam = {
      'dictCode': dict.code,
    };
    getItemPage(this.queryParam).then((res: any) => {
      this.dictData = res.data.records;
    });

    this.editable = dict.editable == 1 ? true : false;
    this.activeIndex = dict.id;
  }

  backToTable() {
    this.loadDictData(this.dictItem);
  }

  handleItemAdd() {
    this.cardTitle = this.dictItem.title + '-值新增';
    this.$nextTick(function() {
      this.formItemPage.add();
    });
  }

  handleItemEdit(record: any, title: string) {
    // this.switchPage()
    this.cardTitle = '“' + title + '”' + ' 修改';
    this.$nextTick(function() {
      this.formItemPage.update(record);
    });
  }

  handleItemDel(record: any) {
    delItemObj(record[this.rowKey]).then((res: any) => {
      if (res.code === 200) {
        this.$message.success(res.msg);
        this.loadDictData(this.dictItem);
      } else {
        this.$message.error(res.msg);
      }
    });
  }
}
